	module.exports = {"data":{"markdownRemark":{"html":"<h2>jQuery</h2>\n<p><img src=\"https://upload.wikimedia.org/wikipedia/en/thumb/9/9e/JQuery_logo.svg/250px-JQuery_logo.svg.png\" alt=\"logo\" title=\"jQuery logo\"></p>\n<p>jQuery is the most widely-used JavaScript library, and is used in more than half of all major websites. </p>\n<p>jQuery makes web development easier to use by providing a number of ‘helper’ functions. These help developers to quickly write DOM (Document Object Model) interactions without needing to manually write as much JavaScript themselves.</p>\n<h2>Example</h2>\n<p>When a user clicks on a button, all <p> elements will be hidden:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token function\">$</span><span class=\"token punctuation\">(</span>document<span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">ready</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">function</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">{</span>\n    <span class=\"token function\">$</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"button\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">click</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">function</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">{</span>\n        <span class=\"token function\">$</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"p\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">hide</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<h4>More Information</h4>\n<ul>\n<li><a href=\"https://jquery.com/\">jQuery Home Page</a></li>\n</ul>","fields":{"slug":"/jquery/"},"frontmatter":{"title":"jQuery"}}},"pathContext":{"slug":"/jquery/"}}

